.page {
  --background-color: #eef7ff;
  --text-color: #1c1a26;
  --about-us-label-color: #7f8096;
  --theme-switcher-bg-color: #1c1a26;
  --theme-switcher-color: #eef7ff;
  background-color: var(--background-color);
  max-width: 100vw;
  color: var(--text-color);
  transition: background-color .3s, color .3s;
}

.page--theme--pink {
  --background-color: #ffeefc;
  --text-color: #f9512e;
  --about-us-label-color: #1c1a26;
  --theme-switcher-bg-color: #f9512e;
  --theme-switcher-color: #ffeefc;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.theme-switcher {
  background-color: var(--theme-switcher-bg-color);
  border-radius: 20px;
  width: 50px;
  margin-left: auto;
  margin-right: 32px;
  padding: 5px;
  transition: background-color .3s;
}

@media (width >= 1024px) {
  .theme-switcher {
    margin-right: 64px;
  }
}

.theme-switcher:after {
  content: "";
  background-color: var(--theme-switcher-color);
  border-radius: 50%;
  width: 50%;
  padding-bottom: 50%;
  transition: transform .3s;
  display: block;
}

.theme-switcher--theme--pink:after {
  transform: translateX(100%);
}

